import { Pipe, PipeTransform } from '@angular/core';
import { QuizStatus } from '../../content/quiz/enums/quiz.enum';

@Pipe({
  name: 'quizStatus'
})
export class QuizStatusPipe implements PipeTransform {

  transform(type: string): string {
    const typeMap: any = {
      [QuizStatus.checking]: '資料審核中',
      [QuizStatus.paying]: '繳款中',
      [QuizStatus.succuss]: '報名成功',
      [QuizStatus.paid]: '已繳款',
      [QuizStatus.noStatus]: '-',
    };
    return typeMap[type];
  }

}
