import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { MercCalendarComponent } from 'merc-lib';
import { CalendarOptions } from '@fullcalendar/angular';
import { EventInput } from '@fullcalendar/core'; // is important!
import * as moment from 'moment';
import { DatesSetArg, EventClickArg } from '@fullcalendar/angular'; // is important!
import { NzModalService } from 'ng-zorro-antd/modal';
import { DateClickArg } from '@fullcalendar/interaction';
import { CalendarEventModalComponent } from './modal/calendar-event-modal/calendar-event-modal.component';
import { EVENT_COLOR } from './const/event-color.const';


@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})


export class CalendarComponent implements OnInit, AfterViewInit {
  @ViewChild('rocCalendar', { static: true }) rocCalendar!: MercCalendarComponent;

  /**
   * 月曆外觀設置及事件觸發設定
   * @references https://fullcalendar.io/docs/angular
   */
  calendarOptions: CalendarOptions = {
    navLinks: false,
  };
  title!: string;
  radioValue = 'today';
  constructor(
    private modalService: NzModalService,
  ) { }

  ngOnInit(): void {
    // 日期點擊事件
    this.rocCalendar.calendarOptions.dateClick = this.onDateClick.bind(this);
    // 事件點擊事件
    this.rocCalendar.calendarOptions.eventClick = this.onEventClick.bind(this);
    // 月曆開始渲染的時候
    this.rocCalendar.calendarOptions.datesSet = this.onDatesRender.bind(this);
  }
  ngAfterViewInit(): void {

  }

  onDateClick(arg: DateClickArg): void {
    console.log('click date: ', arg.dateStr);
  }

  onEventClick(arg: EventClickArg): void {
    console.log(arg.event.title, ': ', arg);
    const modal = this.modalService.create({
      nzContent: CalendarEventModalComponent,
      nzClosable: true,
      nzFooter: null,
      nzClassName: 'white-close-x',
      nzBodyStyle: {
        padding: '0',
      },
      nzComponentParams: {
        arg
      }
    })
  }
  /**
   * 月曆開始渲染的時候
   * @param arg DatesSetArg
   */
  onDatesRender(arg: DatesSetArg): void {
    this.getCalendarEvents();
  }

  /**
   * 取得Event
   * @reference Event Object https://fullcalendar.io/docs/event-object
   */
  getCalendarEvents(): void {
    console.log('rendar Event');

    // TODO: for demo假資料
    const result: EventInput = [
      {
        id: 'a',
        title: '投資型考試',
        start: moment(new Date()).format('yyyy-MM-DD'),
        end: moment(new Date()).add(3, 'day').format('yyyy-MM-DD'),
        color: EVENT_COLOR.danger
      },
      {
        id: 'b',
        title: '行銷工具班 - 行動辦公室',
        start: moment(new Date()).add(-7, 'day').format('yyyy-MM-DD'),
        end: moment(new Date()).add(-5, 'day').format('yyyy-MM-DD'),
        color: EVENT_COLOR.normal
      }
    ];
    this.rocCalendar.calendarOptions.events = result;
  }
}
