import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CalendarComponent } from './content/home/calendar/calendar.component';
import { ROUTING_PATH } from './content/const/routing-path.const';
import { CourseComponent } from './content/course/course.component';
import { AuthGuard } from './core/guard/auth.guard';
import { HomeComponent } from './content/home/home.component';
import { IndexPageComponent } from './content/index-page/index-page.component';

const routes: Routes = [
  {
    path: ROUTING_PATH.ADD_PERSONAL_INFO,
    loadChildren: () => import('./content/add-personal-info/add-personal-info.module').then(m => m.AddPersonalInfoModule),
    // component: AddPersonalInfoComponent,
    canActivate: [AuthGuard]
  },
  {
    path: ROUTING_PATH.QUIZ,
    loadChildren: () => import('./content/quiz/quiz.module').then(m => m.QuizModule),
    canActivate: [AuthGuard]
  },
  {
    path: ROUTING_PATH.COURSE,
    component: CourseComponent,
    canActivate: [AuthGuard]
  },
  {
    path: ROUTING_PATH.PERSONAL_INFO,
    loadChildren: () => import('./content/personal-info/personal-info.module').then(m => m.PersonalInfoModule),
    canActivate: [AuthGuard]
  },
  {
    path: ROUTING_PATH.PROJECTS, loadChildren: () => import('./content/projects/projects.module').then(m => m.ProjectsModule),
    canActivate: [AuthGuard]
  },
  // {
  //   path: ROUTING_PATH.HOME,
  //   component: App,
  //   canActivate: [AuthGuard]
  // },
  {
    path: ROUTING_PATH.HOME,
    component: HomeComponent,
    canActivate: [AuthGuard]
  },
  {
    path: '',
    component: IndexPageComponent,
  },
  {
    path: '**',
    component: CalendarComponent,
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
