import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'courseStatus'
})
export class CourseStatusPipe implements PipeTransform {

  transform(value: number, ...args: unknown[]): string {
    switch (value) {
      case 0:
        return '已結訓';
      case 1:
        return '我要報名';
      case 2:
        return '取消報名';
      default:
        return '未定義';
    }
  }

}
