import { Component, HostListener, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '../../environments/environment';
import { DEVICE } from '../../../../tc-lib/src/lib/const/device.const';
import { COMMON } from '../content/const/global-constants.const';
import { ROUTING_PATH } from '../content/const/routing-path.const';
import { AccountType } from '../core/enums/account-type.enum';
import { AccountService } from '../core/services/account.service';
import { PermissionService } from '../core/services/permission.service';
import { SharedService } from '../shared/shared.service';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {

  /**
   * 版本號
   */
  currentAppVersion = environment.appVersion;
  DEVICE = DEVICE;
  /**
   * 是否已切換為 pad 以上的尺寸
   */
  isPad = true;
  AccountType = AccountType;
  /**
   * routing 路徑
   */
  get ROUTING_PATH(): any { return ROUTING_PATH; }
  /** TODO: for demo */
  get showMenu(): boolean {
    if (this.accountService.account.account_type) {
      return this.accountService.account.account_type !== AccountType.new;
    }
    return false;
  }
  constructor(
    public accountService: AccountService,
    public sharedService: SharedService,
    private router: Router,
    private route: ActivatedRoute,
    private permissionService: PermissionService
  ) { }

  ngOnInit(): void {
    this.initUserDevice(document.documentElement.clientWidth);
    // this.accountService.initCanActivatePaths();
    // this.permissionService.initCanActivatePaths();
  }

  @HostListener('window:resize', ['$event'])
  onResize(event: any): void {
    const size = document.documentElement.clientWidth;
    this.detectScreenSize(size);
  }

  private initUserDevice(size: number): void {
    // 網頁版
    if (size >= 768) {
      this.sharedService.setUserDeviceSubject = DEVICE.PAD;
      // 手機版
    } else if (size > 0 && size < 768) {
      this.sharedService.setUserDeviceSubject = DEVICE.MOBILE;
    }
  }

  private detectScreenSize(size: number): void {
    // 網頁版
    if (size >= 768) {
      if (!this.isPad) {
        this.sharedService.setUserDeviceSubject = DEVICE.PAD;
        this.isPad = true;
      }
      // 手機版
    } else if (size > 0 && size < 768) {
      if (this.isPad) {
        this.sharedService.setUserDeviceSubject = DEVICE.MOBILE;
        this.isPad = false;
      }
    }
  }

  /**
   * 是否登入
   */
  isLogin(): boolean {
    // if (localStorage.getItem(COMMON.TOKEN)) {
    //   return true;
    // }
    // return false;
    return true;
  }

  onLogoutClick(): void {
  }

  /** TODO:  account_type 可能需要調整  */
  onHomeClick(): void {
    this.router.navigate([ROUTING_PATH.HOME]);

    // if (this.accountService.account.account_type === AccountType.new) {
    //   this.router.navigate([ROUTING_PATH.ADD_PERSONAL_INFO]);
    // } else {
    //   this.router.navigate([ROUTING_PATH.HOME]);
    // }
  }

  /**
   * for user 測試用
   */
  onAccountTypeChange(value: AccountType): void {
    switch (value) {
      case AccountType.new:
        this.accountService.account.account_type = AccountType.new;
        // this.permissionService.initCanActivatePaths();
        // this.router.navigate([ROUTING_PATH.ADD_PERSONAL_INFO]);
        break;
      case AccountType.newRecruit:
        this.accountService.account.account_type = AccountType.newRecruit;
        // this.permissionService.initCanActivatePaths();
        // this.router.navigate([ROUTING_PATH.HOME]);
        break;
      case AccountType.sales:
        this.accountService.account.account_type = AccountType.sales;
        // this.permissionService.initCanActivatePaths();
        // this.router.navigate([ROUTING_PATH.HOME]);
        break;
      default:
        break;
    }
    this.router.navigate([ROUTING_PATH.HOME]);

    this.permissionService.canActivatePath.complete();
  }

}
