import { Injectable } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { ROUTING_PATH } from '../../content/const/routing-path.const';
import { AccountType } from '../enums/account-type.enum';
import { AccountService } from './account.service';

@Injectable({
  providedIn: 'root'
})
export class PermissionService {
  /**
   * 身分可進入的頁面
   */
  canActivatePath = new ReplaySubject<string[]>();
  isReadonly!: boolean;
  constructor(
    private accountService: AccountService,

  ) { }
  /**
   * TODO: for demo，目前沒作用
   * 依取得身分別判斷可進入的頁面
   */
  initCanActivatePaths(): void {
    console.log('initCanActivatePaths');
    
    const accountType = this.accountService.account.account_type;
    switch (accountType) {
      case AccountType.sales:
        this.canActivatePath.next([
          ROUTING_PATH.HOME,
          ROUTING_PATH.PERSONAL_INFO,
          ROUTING_PATH.COURSE,
          ROUTING_PATH.QUIZ,
          ROUTING_PATH.PROJECTS,
        ]);
        break;
      case AccountType.newRecruit:
        this.canActivatePath.next([
          ROUTING_PATH.HOME,
          ROUTING_PATH.PERSONAL_INFO,
          ROUTING_PATH.COURSE,
          ROUTING_PATH.QUIZ
        ]);
        break;
      case AccountType.new:
        this.canActivatePath.next([
          ROUTING_PATH.ADD_PERSONAL_INFO,
        ]);
        break;
      default:
        console.warn('沒拿到 account type');
        this.canActivatePath.next(['']);
        break;
    }
  }

  /**
   * 取得身分可進入的頁面
   */
  getPath(): Observable<string[]> {
    return this.canActivatePath.asObservable();
  }
}
