import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SharedService } from 'projects/tc-back/src/app/shared/shared.service';
import { DEVICE } from 'projects/tc-lib/src/lib/const/device.const';
import { ICON_PATH } from '../../const/icon-path.const';
import { ROUTING_PATH } from '../../const/routing-path.const';

@Component({
  selector: 'app-apply-home',
  templateUrl: './apply-home.component.html',
  styleUrls: ['./apply-home.component.scss']
})
export class ApplyHomeComponent implements OnInit {
  /**
   * TODO: 假資料
   */
  // applyList = [{ name: '阿嚕嚕/A123456789', apply_date: '2021/05/24', status: '尚未送出申請' }];
  applyList: any[] = [];
  DEVICE = DEVICE;
  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  constructor(
    public sharedService: SharedService,
    private router: Router,
  ) { }

  ngOnInit(): void {
  }
  onEditClick(): void {
    this.router.navigate([ROUTING_PATH.ADD_PERSONAL_INFO]);
  }
  /**
   * TODO: for demo
   * - unSend 尚未送出申請
   * - return 退件
   * - noData 還沒開始申請
   */
  onMockListChange(key: string): void {
    this.applyList = [];
    switch (key) {
      case 'unSend':
        this.applyList = [{ name: '阿嚕嚕/A123456789', apply_date: '2021/05/24', status: '尚未送出申請' }];;
        break;
      case 'return':
        this.applyList = [{ name: '阿嚕嚕/A123456789', apply_date: '2021/05/30', status: '退件' }];
        break;
      case 'noData':
        this.applyList = [];
        break;
      default:
        break;
    }
  }
}
