import { Pipe, PipeTransform } from '@angular/core';
import { AccountType } from '../../core/enums/account-type.enum';
@Pipe({
  name: 'accountTypePipe'
})
export class AccountTypePipe implements PipeTransform {

  /** TODO: 等後端開好才知道是string還是number */
  transform(type: string | number): string {
    const typeMap: any = {
      [AccountType.new]: '新增員',
      [AccountType.newRecruit]: '新聘員',
      [AccountType.sales]: '業務員',
    };
    return typeMap[type] ? typeMap[type] : '未選擇';
  }

}
