
import { NzModalRef } from 'ng-zorro-antd/modal';
import { ICON_PATH } from '../../../content/const/icon-path.const';
import { Component, Input, OnInit } from '@angular/core';
import { REGISTER_TYPE } from '../../../content/const/register-type.const';
import { RegisterAction } from '../../../core/enums/register-action.enum';

@Component({
  selector: 'app-singup-modal',
  templateUrl: './singup-modal.component.html',
  styleUrls: ['./singup-modal.component.scss']
})
export class SingupModalComponent implements OnInit {
  /** 確認報名 or 取消報名 */
  @Input() action!: number;
  /** 課程資訊 */
  @Input() event: any;
  /** 報名課程 or 報名考試 */
  @Input() type = 'course';

  get ICON_PATH() {
    return ICON_PATH;
  }
  REGISTER = REGISTER_TYPE;

  /** 文字顯示課程 or 測驗 */
  typeWording!: string;

  // 報名操作enum
  RegisterAction = RegisterAction;

  constructor(
    public modalRef: NzModalRef
  ) {
  }

  ngOnInit(): void {
    if (this.type === REGISTER_TYPE.COUSRE) {
      this.typeWording = '課程';
    } else {
      this.typeWording = '測驗';
    }

    this.event = { subject: '行銷工具班 - 行動辦公室鳳翔日誌篇', location: '行銷工具班 - 行動辦公室鳳翔日誌篇', date: '110/03/04（四）～110/03/05（五）', time: '09:00～18:00' };
  }


  onSendClick() {
    this.modalRef.triggerOk();
  }
}
