import { DEVICE } from 'projects/tc-lib/src/lib/const/device.const';
import { SharedService } from 'projects/tc-front/src/app/shared/shared.service';
import { ICON_PATH } from './../../../content/const/icon-path.const';
import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-state-light',
  templateUrl: './state-light.component.html',
  styleUrls: ['./state-light.component.scss']
})
export class StateLightComponent implements OnInit {
  /**
   * 滿分多少
   */
  @Input() totalState = 5;
  /**
   * 目前狀態
   */
  @Input() state = 3;

  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  DEVICE = DEVICE;
  stateArray = new Array(this.totalState);
  constructor(
    public sharedService: SharedService
  ) { }

  ngOnInit(): void {
    this.stateArray.fill(true);
    this.stateArray.forEach((s, index) => this.stateArray[index] = (index + 1) <= this.state)
  }

}
